/*===============TOGGLE===================*/
.toggle-outer{
    width:24px;
    background-color: blue;
    height: 14px;
    border-radius: 7px;
    margin-right:5px;
     
    -webkit-transition-duration: 200ms;
     
            transition-duration: 200ms;
}
.toggle-outer:hover{
    cursor: pointer;
}

.outer-inactive{
    background-color: gray;
}

.toggle-inner{
    width:12px;
    height: 12px;
    border-radius: 6px;
    margin-top:1px;
    margin-left:1px;
    -webkit-transition-duration: 200ms;
            transition-duration: 200ms;
    background-color: white;
   
}

.inner-inactive{
    margin-left:11px;
}
.menu-ul {
    text-align: left;
    list-style: none;
    padding: 0;
    width: 150px;
    background-color: rgb(0,145,60);
 
